import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import { BrowserRouter } from "react-router-dom";
import { Provider } from "react-redux";
import { combineReducers, createStore } from "redux";
import * as serviceWorkerRegistration from "./serviceWorkerRegistration";

// 1.   (state)
let cartState = [
  { id: 4, name: "", quan: 2 },
  { id: 5, name: "", quan: 6 },
  { id: 6, name: "", quan: 1 },
];

let alertToggle = true;

function reducer2(state = alertToggle, action) {
  state = action.type ? true : false;
  return state;
}

// 2.    (stateModify)
function reducer(state = cartState, action) {
  // action   state   
  // 1.  id    ,  
  // 2.          OK
  if (action.type === "addCart") {
    let temp = state.findIndex((data) => data.id === action.payload.id);

    if (temp !== -1) {
      let copy = [...state];
      copy[temp].quan++;
      return copy;
    }

    let copy = [...state];
    copy.push({
      id: action.payload.id,
      name: action.payload.title,
      quan: action.inven,
    });
    return copy;
  }

  if (action.type === "increase") {
    let copy = [...state];
    let indexValue = copy.findIndex((c) => c.id === action.id);
    copy[indexValue].quan++;
    return copy;
  }

  if (action.type === "decrease") {
    let copy = [...state];
    let index = copy.findIndex((c) => c.id === action.id);
    copy[index].quan === 1 ? (copy[index].quan = 1) : copy[index].quan--;
    return copy;
  }

  return state;
}

// 3. stateModify createStore()  
let store = createStore(combineReducers({ reducer, reducer2 }));

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
